<template>
	<div>
		<search/>
		<div style="height: 50px;"></div>

		<van-badge-group :active-key="activeKey" class="tab" :style="`height: ${fullHeight}px`">
			<van-badge v-for="(item, index) in goodsGroupList" :key="index" :title="item.Name" @click="onClick"/>
		</van-badge-group>

		<div class="content" :style="`width: ${fullWidth - 14}px; height: ${fullHeight - 7}px`">
			<div class="category-div">
				<h4>{{goodsGroupList[activeKey].Name}}</h4>
				<ul>
					<li v-for="(item, index) in goodsGroupList[activeKey].children" :key="index">
						<div @click="searchCategory(item)">
							<img src="../../static/img/goodsList/tag.png">
							<span>{{item.Name}}</span>
						</div>
					</li>
					<div style="clear:both"></div>
				</ul>
			</div>
		</div>
		<navigate/>
	</div>
</template>

<script>
	import navigate from '../components/footer/navigate.vue'
	import search from "../components/page/search.vue";
	import {mapState, mapActions, mapMutations} from 'vuex';

	export default {
		name: "Category",

		components: {
			search,
			navigate,
		},

		data() {
			return {
				activeKey: 0,
				fullHeight: document.documentElement.clientHeight - 100,
				fullWidth: document.documentElement.clientWidth - 80,
			};
		},

		computed: {
			...mapState(['goodsGroupList'])
		},

		async mounted() {
			this.$store.commit('SET_TABBAR_INDEX', 1);
			await this.getGoodsGroupList();
		},

		methods: {
			...mapActions(['getGoodsGroupList', 'loadSearchResult']),
			...mapMutations(['SET_SEARCH_KEY', 'SET_SEARCH_CATEGORY', 'SET_SEARCH_TYPE']),

			onClick(key) {
				this.activeKey = key;
			},

			searchCategory(item) {
				this.SET_SEARCH_KEY('');
				this.SET_SEARCH_CATEGORY(item.ID);
				this.SET_SEARCH_TYPE([null, null, null, null]);
				this.$router.push("/goodsList");
			},
		}
	};
</script>

<style lang="less">
	.tab {
		width: 80px;
		float: left;
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
		min-height: 100%;
		.van-badge {
			padding: 15px 12px 15px 9px;
		}
		.van-badge:not(:last-child)::after {
			height: 199%;
		}
	}

	.content {
		float: left;
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
		min-height: 100%;
		margin: 7px 7px 0;
		font-size: 12px;
		img {
			width: 100%;
		}
		.category-div {
			margin: 19px 0px 0;
			h4 {
				font-size: 14px;
				color: #333;
			}
			ul {
				margin-top: 10px;
			}
			li {
				width: 32.8%;
				float: left;
				text-align: center;
				img {
					width: 60px;
					height: 60px;
				}
				span {
					font-size: 12px;
					height: 36px;
					color: #686868;
					width: 100%;
					overflow: hidden;
					text-overflow: ellipsis;
					display: box;
					display: -webkit-box;
					display: -moz-box;
					display: -ms-box;
					display: -o-box;
					line-clamp: 2;
					-webkit-line-clamp: 2;
					-moz-line-clamp: 2;
					-ms-line-clamp: 2;
					-o-line-clamp: 2;
					box-orient: vertical;
					-webkit-box-orient: vertical;
					-ms-box-orient: vertical;
					-o-box-orient: vertical;
					word-break: break-all;
					box-align: center;
					-webkit-box-align: center;
					-moz-box-align: center;
					-ms-box-align: center;
					-o-box-align: center;
					box-pack: center;
					-webkit-box-pack: center;
					-moz-box-pack: center;
					-ms-box-pack: center;
					-o-box-pack: center;
					position: relative;
				}
			}
		}
	}
</style>



